/* To specify background color and border */
$main-color:#2d4341;
$second-color:#4b978b;

.reservation-page{
    font-size: 1.5vw;
    form{
        margin-top: 5%;
        padding: 2%;
        width: 40%;
        height: 56vh;
        display: grid;
        grid-template-columns: 50% 50%;
        button{
            font-size: 4vw;
            height: 10vh;
            background-color: white;
            border: solid $second-color;
            &:hover{
                background-color: $second-color;
            }
        }
        label{
            padding: 01%;
            input , textarea{
                width: 100%;
                height: 5vh;
                border:solid $second-color;
            }
            textarea{
                height: 20vh;
            }
        }
    }
}

.e-calendar {
    font-size:4vw;
    text-align: center;
    
    .e-icon-container{
        button{
            display: block;
        }
    }
    button{
        display: none;
    }
    .e-title{
        width: 100%;
    }
    .e-content{
        width: fit-content;
        height: fit-content;
        
        table{
            margin: auto;
            width: 100%;
            background-color: $second-color;
            thead{
                width: 100%;
                th{
                    width: 100%/7;
                }
            }
            tbody{
                tr{
                    td{
                       
                        span{
                            
                            display: block;
                            width: 100%/7;
                            background-color: white;
                            
                            &:hover{
                                background-color: $second-color;
                            }
                        }
                    }
                }
            }
            
        }
    }
}

/* To specify background color and color  */
.e-calendar .e-content td.e-focused-date.e-today span.e-day {
    background-color: $second-color !important;
    color: #fff;
}
.e-focused-date , .e-selected{
    background-color: $second-color 
}