$main-color:#2d4341;
$second-color:#4b978b;


.menu-page{
    position: relative; 
    display: block;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    .menu{
        margin-bottom: 5%;
        margin-top: -2%;
        height: fit-content;
        background-color: #4b978b;
        border: solid $main-color;
    }
    button{
        padding: 1vw;
        margin-left: 0.5%;
        border-radius: 10%;
        
    }
    .active{
            background-color: $second-color;
        }
    span{
        margin-top: 2%;
        margin-bottom: 2%;
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        h1 , h2 , h3{
            width: 33%;
        }
    }
}